body {
  display: flex;
  justify-content: center;
}

input,
button {
  line-height: 1rem;
  font-size: 1rem;
  padding: 5px 15px;
  border-radius: 5px;
  outline: 0;
  margin: 2px 0px;
}

input {
  display: inline-block;
}

label{
  display: block;
  font-size: 1rem;
  font-weight: bold;
  margin: 2px 0px;
}

button {
  margin: 0.5rem;
  padding: 0.5 1rem;
  font-weight: bold;
  box-shadow: 0px 2px 2px lightgray;
}

text{
	font-weight: bold;
}